@font-face {
  font-family: 'FontSlava';
  src: url('./fonts/FontSlava.ttf') format('ttf');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'FontSlava1';
  src: url('./fonts/FontSlavaNum.ttf') format('ttf');
  font-weight: bold;
  font-style: normal;
}

body {
  background-color: #CCCCCC;
  font-family: 'FontSlava', cursive;;
  color: black;
  height: 100vh;
  margin: 0;
}

h3 {
  margin-bottom: 0px;
  margin-top: 10px;
  font-size: 30px;
  font-weight: 100;
}

input,
button,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

.notebook {
  @media (max-width:760px) {
    width: 580px;
  }
}